$(window).load(function() {
  $("input.hs-button").addClass("btn btn-default btn-xl sr-button");
  var e = window.location.href.split("/").slice(-1)[0];
  $("select[name='case_study_selection'] option[value='" + e + "']").prop("selected", true).change();
  var g = navigator.userAgent || navigator.vendor || window.opera;
  var f = (/iPad|iPhone|iPod/.test(g) && !window.MSStream);
  if ((window.matchMedia("(min-width: 769px)").matches) && !f) {
    var d = $(".left-case-study-section").position().top - $(window).height() / 2 + $(".left-case-study-section").height() / 2;
    if ($("#numbers-section-2")[0] === undefined) {
      var c = $(document).height() - $("#testimony-section").position().top + 150
    } else {
      var c = $(document).height() - $("#numbers-section-2").position().top + 150
    }
    $(".left-case-study-section").affix({
      offset: {
        top: d,
        bottom: c
      }
    }).on("affix.bs.affix", function() {
      $(this).css({
        width: $(this).outerWidth()
      })
    })
  }
  if (window.matchMedia("(max-width: 769px)").matches || f) {
    $("#case-study-section > .row > .left-case-study-section")[0].style.margin = "0 auto";
    $("#case-study-section > .row > .left-case-study-section")[0].style.float = "none";
    $("#case-study-section > .row > .left-case-study-section")[0].style.width = "100%";
    if (window.matchMedia("(min-width: 768px)").matches) {
      $(".request-more-information-form")[0].style.width = "345px";
    }
    $("#case-study-section > .row > .case-study-block")[0].style.width = "100%"
  }
  var b = $(window).scrollTop();
  var h = $(".process-section").offset().top - $(window).height() / 2;
  var a = $(".impact-section").offset().top - $(window).height() / 2;
  if (b >= h) {
    $("#case-study-section").removeClass("bg-g1").addClass("bg-o1");
    $(".left-case-study-section .section-header").removeClass("text-g2").addClass("text-g1")
  }
  if (b < h) {
    $("#case-study-section").removeClass("bg-o1").addClass("bg-g1");
    $(".left-case-study-section .section-header").removeClass("text-g1").addClass("text-g2")
  }
  if ((b >= a) && (b >= h)) {
    $("#case-study-section").removeClass("bg-o1").addClass("bg-g2")
  }
  if ((b < a) && (b >= h)) {
    $("#case-study-section").removeClass("bg-g2").addClass("bg-o1")
  }
});
$(document).ready(function() {
  $("form").submit(function() {
    var a = window.location.href.split("/").slice(-1)[0];
    $("select[name='case_study_selection'] option[value='" + a + "']").prop("selected", true).change()
  });
  $(window).scroll(function() {
    var b = $(window).scrollTop();
    var c = $(".process-section").offset().top - $(window).height() / 3;
    var a = $(".impact-section").offset().top - $(window).height() / 2;
    if (b >= c) {
      $("#case-study-section").removeClass("bg-g1").addClass("bg-o1");
      $(".left-case-study-section .section-header").removeClass("text-g2").addClass("text-g1")
    }
    if (b < c) {
      $("#case-study-section").removeClass("bg-o1").addClass("bg-g1");
      $(".left-case-study-section .section-header").removeClass("text-g1").addClass("text-g2")
    }
    if ((b >= a) && (b >= c)) {
      $("#case-study-section").removeClass("bg-o1").addClass("bg-g2")
    }
    if ((b < a) && (b >= c)) {
      $("#case-study-section").removeClass("bg-g2").addClass("bg-o1")
    }
  })
});
